import { ModalRenderer } from '../components/ModalRenderer';
import * as modals from '../components/modals';

import React, {
  createContext,
  useCallback,
  useContext,
  useState,
  ReactNode,
} from 'react';

export type KnownModals = typeof modals;
export type KnownModalId = keyof KnownModals;
export type ModalId = KnownModalId | (string & {});

interface ModalState {
  id: ModalId | null;
  props?: Record<string, any>;
}

interface ModalContextValue {
  modal: ModalState;
  openModal: (id: ModalId, props?: Record<string, any>) => void;
  closeModal: () => void;
}

const ModalContext = createContext<ModalContextValue | null>(null);

export const ModalProvider = ({ children }: { children: ReactNode }) => {
  const [modal, setModal] = useState<ModalState>({ id: null });

  const openModal = useCallback((id: ModalId, props?: Record<string, any>) => {
    setModal({ id, props });
  }, []);

  const closeModal = useCallback(() => {
    setModal({ id: null });
  }, []);

  return (
    <ModalContext.Provider value={{ modal, openModal, closeModal }}>
      {children}
      <ModalRenderer />
    </ModalContext.Provider>
  );
};

export const useModalContext = () => {
  const context = useContext(ModalContext);
  if (!context) {
    throw new Error('useModalContext must be used within ModalProvider');
  }
  return context;
};
